import React from 'react';

import styles from './css/article.css';

import LoadingItem from './component/loading';

import ArticleItem from './component/articleItem';

import Pagination from './component/pagination';

import { browserHistory, Link } from 'react-router';

//console.log(browserHistory);

//browserHistory.push('/about');;

export default class ArticleList extends React.Component {
	constructor(props, context) {
		super(props, context);
		var pageNumber = this.props.params.number ? parseInt(this.props.params.number) : 1; //
		var totalNumber = 1; //
		var loading = true; //
		this.state = {
			article: [],
			totalNumber: totalNumber,
			activeNumber: pageNumber,
			loading: loading
		};

		console.log(this.props.route);
	}

	componentWillMount() {
	}

	routerLeaveInformation() {
		return 'You have unsaved information, are you sure you want to leave this page?';
	}


	componentDidMount() {
		//1
		var pageNumber = this.state.activeNumber;
		this.fetchArticleData(pageNumber);
		this.fetchTotalNumber();
	}

	fetchTotalNumber() {
		fetch('/api/totalpagenumber/', {
			method: 'GET',
		}).then(
			res => res.text()
		).then(
			data => {
				this.setState({
					totalNumber: data
				});
			}
		);		
	}

	fetchArticleData(pageNumber) {
		fetch('/api/articlelist/' + pageNumber, {
			method: 'GET',
			Accept: 'application/json'
		}).then(
			res => res.json()
		).then(
			data => {
				//console.log(Object.prototype.toString.call(data));
				//console.log(data);
				this.setState({
					article: data,
					loading: false
				});
			}
		);		
	}

	handlePageChange(pageNumber) {
		browserHistory.push('/articlelist/' + pageNumber); // context.router
		this.setState({
			loading: true,
			//activeNumber: parseInt(newNumber)
		});
		// this.fetchArticleData(pageNumber);
		// articleListarticlebug
		// navlinkhistory.pushstatereplace
		// componentWillReceiveProps
		// context.routercomponentWillReceivePropsshouldComponentUpdate
		// componentWillUpdaterendercomponentDidUpdatewillReceive
	}

	listenUrl() {
		// 
		// var _this = this;
		// urlreact-router4navbug
		// articlehistory.pushstate
		// articleList
		// console.log(this.context.router);
		// this.context.router.listen( (location) => {
		// 	var locationArr = location.pathname.split('/');
		// 	var param = locationArr[locationArr.length - 1];
		// 	_this.fetchArticleData(param);
		// });
	}

	componentWillReceiveProps() {
		var oldNumber = this.props.params.number;
		var newNumber = this.props.router.params.number;
		if (oldNumber != newNumber) {
			this.setState({
				activeNumber: parseInt(newNumber)
			});
			this.fetchArticleData(newNumber);
		}
	}


	render() {
		var data = this.state.article; // this.state.articledataredux
		
		var node = data.map( (item, index) => {
			return (
				<ArticleItem key={index} article={item} />
			)
		} );

		var component = this.state.loading ? 
			(<LoadingItem />) : 
			(<ul className={styles.articleList}>
				{node}
				<Pagination 
					onChange={(pageNumber) => this.handlePageChange(pageNumber)}
					activeNumber={this.state.activeNumber}
					totalNumber={this.state.totalNumber} />

			</ul>);

		return (
			<div className={styles.articleListContainer}>
				{component}
			</div>
		)
	}
}

ArticleList.contextTypes = {
	router: React.PropTypes.object.isRequired
};


